.manager-page {
  background-color: #EEEFE0;
  color: #2f3e34;
  min-height: 100vh;
  padding: 2rem;
  font-family: "Poppins", sans-serif;
}

h1 {
  color: #819A91;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 2rem;
}

th {
  background-color: #A7C1A8;
  color: white;
  padding: 10px;
}

td {
  background-color: #D1D8BE;
  padding: 10px;
  text-align: left;
  border-bottom: 2px solid #EEEFE0;
}
